* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  padding: 0;
  margin: 0;
  line-height: 1.4;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  text-decoration: none;
}
.graph-resolution {
  height: 230px !important;
}

.display-data {
  font-size: 10px;
  display: flex;
  flex-direction: column;
}
.divider {
  width: 120px;
  margin-top: 5px;
  margin-bottom: 5px;
  border: 1px solid grey;
}
.additionalData {
  display: flex;
  justify-content: center;
  margin-right: 70px;
}

.additionalDataToday {
  border: 1px solid grey;
  border-radius: 15px;
  padding: 5px;
  margin: 0px 150px;
  min-width: 150px;
}
.additionalDataYesterday {
  border: 1px solid grey;
  border-radius: 15px;
  padding: 5px;
  margin: 0px 150px;
  min-width: 150px;
}

@media screen and (max-width: 990px) {
  .additionalDataToday,
  .additionalDataYesterday {
    margin: 0px 90px;
  }
}

@media screen and (max-width: 768px) {
  .additionalDataToday,
  .additionalDataYesterday {
    margin: 0px 50px;
  }
}
